.section-title {
  margin-left: 20px;
  font-size: 20px;
  font-weight: bold;
  color: #4a4a4a;
}

.post {
  display: flex;
  border: 1px solid rgba(0, 0, 0, 0.3);
  width: 500px;
  padding: 10px;
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  margin-bottom: 20px;
  margin-left: 20px;
}

.img-thumb {
  width: 200px;
  height: 150px;
  margin-right: 20px;
}

.img-thumb img {
  width: 200px;
  height: 150px;
  object-fit: cover;
}

.content {
  flex: 1;
}

.content p.title {
  margin: 0px;
  font-size: 20px;
  font-weight: bold;
  color: #3d4a6e;
  margin-bottom: 16px;
}

.content p.desc {
  margin: 0px;
  font-size: 16px;
  color: #4a4a4a;
}

.btn-delete {
  /* width: 100px; */
  margin-top: 10px;
  border: none;
  background-color: red;
  font-size: 14px;
  color: white;
  padding: 10px 30px;
  font-weight: 600;
  border-radius: 6px;
  cursor: pointer;
}
